import { OB11BaseNoticeEvent } from '../notice/OB11BaseNoticeEvent'
import { EventType } from '../OB11BaseEvent'

export class OB11GroupRequestEvent extends OB11BaseNoticeEvent {
  post_type = EventType.REQUEST
  request_type = 'group'
  sub_type: 'add' | 'invite'  // invite  bot 
  comment: string
  flag: string
  group_id: number
  user_id: number  //  sub_type  invite  user_id  QQ 
  invitor_id: number // https://github.com/Mrs4s/go-cqhttp/blob/master/coolq/event.go#L566

  constructor(groupId: number, userId: number, flag: string, comment: string, subType: 'add' | 'invite', invitorId: number = 0) {
    super()
    this.group_id = groupId
    this.user_id = userId
    this.comment = comment
    this.flag = flag
    this.sub_type = subType
    this.invitor_id = invitorId
  }
}
