import ffmpeg from 'fluent-ffmpeg'
import fs from 'node:fs'
import { log } from './legacyLog'
import { Context } from 'cordis'
import path from 'node:path'
import { fileURLToPath } from 'node:url'

const defaultVideoThumbB64 =
  ''

export const defaultVideoThumb = Buffer.from(defaultVideoThumbB64, 'base64')

export async function getVideoInfo(filePath: string) {
  const size = fs.statSync(filePath).size
  return new Promise<{
    width: number
    height: number
    time: number
    format: string
    size: number
    filePath: string
  }>((resolve, reject) => {
    ffmpeg(filePath).ffprobe((err, metadata) => {
      if (err) {
        reject(err)
      } else {
        const videoStream = metadata.streams.find((s) => s.codec_type === 'video')
        resolve({
          width: videoStream?.width!,
          height: videoStream?.height!,
          time: parseInt(videoStream?.duration!),
          format: metadata.format.format_name!,
          size,
          filePath,
        })
      }
    })
  })
}

export function checkFfmpeg(newPath?: string): Promise<boolean> {
  return new Promise((resolve, reject) => {
    log(` FFmpeg ${newPath ?? ''}`)
    if (newPath) {
      ffmpeg.setFfmpegPath(newPath)
    }
    try {
      ffmpeg.getAvailableFormats((err, formats) => {
        if (err) {
          log('FFmpeg is not installed or not found in PATH:', err)
          resolve(false)
        } else {
          log('FFmpeg is installed.')
          resolve(true)
        }
      })
    } catch (e) {
      resolve(false)
    }
  })
}
