import { unlink } from 'node:fs/promises'
import { BaseAction } from '../BaseAction'
import { ActionName } from '../types'
import { checkFileReceived, uri2local } from '../../../common/utils/file'

interface Payload {
  file: string
}

export default class SetQQAvatar extends BaseAction<Payload, null> {
  actionName = ActionName.SetQQAvatar

  protected async _handle(payload: Payload): Promise<null> {
    const { path, isLocal, errMsg } = await uri2local(this.ctx, payload.file)
    if (errMsg) {
      throw new Error(errMsg)
    }
    if (path) {
      await checkFileReceived(path, 5000) // QQ
      const ret = await this.ctx.ntUserApi.setSelfAvatar(path)
      if (!isLocal) {
          unlink(path).then().catch(err => {})
      }
      if (!ret) {
        throw `${payload.file},api`
      }
      if ((ret.result as number) === 1004022) {
        throw `${payload.file}`
      } else if (ret.result !== 0) {
        throw `${payload.file},,${ret['result']}:${ret['errMsg']}`
      }
    } else {
      if (!isLocal) {
        unlink(path).then().catch(e => {
          this.ctx.logger.error(`${payload.file},,`)
        })
      }
    }
    return null
  }
}
